html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 12px; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent; }

section, article {
  display: block; }

html, body, p, ol, ul, li, dl, dt, dd, blockquote, figure, fieldset, legend, textarea, pre, iframe, hr, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

html, body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,"Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei",SimSun, sans-serif; }

html {
  color: #000;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

body {
  padding: 10px; }

ul, li, ol {
  list-style: none; }

a {
  text-decoration: none;
  color: #333;
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
  cursor: pointer; }

input {
  outline: none; }

input::-webkit-input-placeholder {
    color: #aaa; }

button {
  cursor: pointer;
  outline: none; }

#app {
  border: 1px solid #ebebeb;
  height: 100%;
  border-radius: 5px;
  overflow: hidden; }

.task-header {
  height: 48px;
  background: #f6f6f6;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #ebebeb;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.task-header nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-left: 10px;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1; }

.task-header nav a {
      padding: 5px 7px;
      border: 1px solid #ebebeb;
      border-left: none;
      background: #fff;
      font-size: 13px; }

.task-header nav a.active, .task-header nav a:hover {
        background: #63b0f6;
        color: #fff; }

.task-header nav a:first-child {
        border-radius: 5px 0 0 5px;
        border-left: 1px solid #ebebeb; }

.task-header nav a:last-child {
        border-radius: 0 5px 5px 0; }

.task-header .search {
    width: 171px;
    height: 30px;
    margin-right: 10px;
    position: relative;
    border: 1px solid #dedede;
    border-radius: 3px;
    background: #fff; }

.task-header .search input {
      background: transparent;
      width: 100%;
      height: 100%;
      color: #666;
      border: none;
      font-size: 12px;
      padding: 0 26px 0 8px; }

.task-header .search button {
      border: none;
      background-image: url(../static/images/top-bar-search-icon.png);
      background-repeat: no-repeat;
      width: 16px;
      height: 16px;
      position: absolute;
      right: 5px;
      top: 50%;
      margin-top: -8px; }

.icon-filter {
  display: inline-block;
  background: url(../static/images/filter.png) no-repeat;
  width: 13px;
  height: 13px; }

.section-filter {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #ebebeb;
  height: 40px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  padding: 0 10px;
  font-size: 14px;
  color: #999; }

.section-filter .text {
    display: inline-block;
    margin: 0 5px; }
